.block-collection {
  position: relative;
  background-color: #fff;
  top: -64px;
  margin-bottom: -64px;
  border: 1px solid #EAEAEA;
  padding-top: 64px;

  &__title {
    text-align: center;
    &--main {
      font-weight: 300;
      font-size: 14px;
      color: #F16D7F;
      margin-bottom: 12px; }
    &--redline {
      position: relative;
      padding-bottom: 12px;
      &:after {
        position: absolute;
        content: '';
        left: 50%;
        transform: translateX(-50%);
        width: 63px;
        height: 3px;
        background-color: #EF5B70; } }
    &--subtitle {
      font-weight: 300;
      font-size: 18px;
      color: #4D4D4D;
      margin-bottom: 48px; } }


  &__paragraph {
    text-align: center;
    max-width: 555px;
    margin: 0 auto;
    &--text {
      font-weight: 300;
      font-size: 14px;
      color: #5E5E5E;
      margin-bottom: 2rem; } }


  &__price {
    text-align: center;
    margin-bottom: 64px;
    &--text {
      font-weight: 300;
      font-size: 24px;
      color: #EF5B70; } }


  &__line {
    position: relative;
    padding-bottom: 64px;
    &:after {
      position: absolute;
      content: '';
      left: 50%;
      transform: translateX(-50%);
      width: 642px;
      height: 1px;
      background-color: #EAEAEA; } } }






